
import React, { memo, useEffect } from "react";
import styled from "styled-components";
import { sidePadding, mainWrapper, topPaddingMidium, topPaddingLarge } from "../../../styles/wrapper";
import colors from "../../../constants/colors";
import { CreateBgColor } from "../../../containers";
import time from "../../../constants/time";

export interface ContentsProps {
}

/**
 * Styled component.
 */
const Wrapper = styled.div`
  width: 100%;
  background-color: ${ colors.blue };
`;

const Main = styled.div`
  ${ sidePadding }
  ${ topPaddingLarge }
  width: 100%;
  max-width: 1025px;
  background-color: #fff;
  margin: 0 auto;
  min-height: 100vh;
  position: relative;
  padding-bottom: 100px;
`;

const ChildWrapper = styled.div`
  // ${ topPaddingMidium }
  ${ mainWrapper }
`;

const HeaderOvr = styled.div`
  width: 100%;
  top: 0;
  left: 0;
  right: 0;
  padding-top: 92px;
  position: fixed;
  z-index: 1;
  &:before {
    width: 100%;
    height: 100%;
    content: "";
    top: 0;
    left: 0;
    right: 0;
    margin: auto;
    max-width: 1025px;
    position: absolute;
    background-color: #fff;
  }
`;

/**
 * Functional component.
 * @param props 
 */
export const Content: React.FC<ContentsProps> = memo(({
  children
}) => {

  const { setBgColor } = CreateBgColor.useContainer();

  useEffect(() => {
    setTimeout(() => { setBgColor(colors.blue); }, time.transitionAnimation);
  }, []);

  return (
    <>
      <Wrapper>
        <Main>
          <HeaderOvr />
          <ChildWrapper>
            { children }
          </ChildWrapper>
        </Main>
      </Wrapper>
    </>
  );
});
